import { useState, useEffect } from "react";
import { useSnackbar } from "notistack";
import { useNavigate, Link as RouterLink } from "react-router-dom";
// @mui
import {
  Box,
  Card,
  Table,
  Button,
  Switch,
  Tooltip,
  TableBody,
  Container,
  IconButton,
  TableContainer,
  TablePagination,
  FormControlLabel,
} from "@mui/material";
// redux
import { useDispatch, useSelector } from "../../redux/store";
import {
  getAgents,
  deleteAgent,
  deleteAgentSuccess,
  deleteAgents,
  deleteAgentsSuccess,
} from "../../redux/slices/agent";
// routes
import { PATH_DASHBOARD } from "../../routes/paths";
// hooks
import useSettings from "../../hooks/useSettings";
import useTable, { getComparator, emptyRows } from "../../hooks/useTable";
// components
import Page from "../../components/Page";
import Iconify from "../../components/Iconify";
import Scrollbar from "../../components/Scrollbar";
import HeaderBreadcrumbs from "../../components/HeaderBreadcrumbs";
import {
  TableNoData,
  TableSkeleton,
  TableEmptyRows,
  TableHeadCustom,
  TableSelectedActions,
} from "../../components/table";
// sections
import {
  AgentTableRow,
  AgentTableToolbar,
} from "../../sections/@dashboard/agent/agent-list";
import extractErrorMessage from "../../utils/error";
import { useTranslation } from "react-i18next";

// ----------------------------------------------------------------------

export default function AgentListList() {
  const {
    dense,
    page,
    order,
    orderBy,
    rowsPerPage,
    //
    selected,
    setSelected,
    onSelectRow,
    onSelectAllRows,
    //
    onSort,
    onChangeDense,
    onChangePage,
    onChangeRowsPerPage,
  } = useTable({
    defaultOrderBy: "full_name",
    defaultRowsPerPage: 50,
  });

  const { themeStretch } = useSettings();

  const navigate = useNavigate();

  const { t } = useTranslation();

  const TABLE_HEAD = [
    { id: "full_name", label: t("field.full_name"), align: "left" },
    { id: "sold", label: t("app.sold"), align: "center" },
    { id: "sold_htg", label: t("app.sold_htg"), align: "center" },
    { id: "sold_clp", label: t("app.sold_clp"), align: "center" },
    { id: "supervisor", label: t("field.supervisor"), align: "center" },
    { id: "phone", label: t("field.phone"), align: "center" },
    { id: "country_name", label: t("field.country"), align: "center" },
    { id: "city", label: t("field.city"), align: "center" },
    { id: "address", label: t("field.address"), align: "center" },
    { id: "transfer", label: t("intl_transfer_title"), align: "center" },
    { id: "is_block", label: t("field.block"), align: "center" },
    { id: "jackpot", label: "Jackpot", align: "center" },
    { id: "deposit", label: "Deposit", align: "center" },
    { id: "", align: "right" },
  ];

  const dispatch = useDispatch();

  const { enqueueSnackbar } = useSnackbar();

  const { agents, isLoading } = useSelector((state) => state.agent);

  const [tableData, setTableData] = useState([]);

  const [filterName, setFilterName] = useState("");

  const handleRefresh = () => {
    dispatch(getAgents(page + 1, rowsPerPage, filterName));
  };

  useEffect(() => {
    handleRefresh();
  }, [dispatch, page, rowsPerPage, filterName]);

  useEffect(() => {
    setTableData(agents?.data);
  }, [agents]);

  const handleFilterName = (value) => {
    setFilterName(value);
    onChangePage(null, 0);
  };

  const handleDeleteRow = async (id) => {
    try {
      const result = await deleteAgent(id);
      enqueueSnackbar(t(`success.${result}`));
      dispatch(deleteAgentSuccess(id));
    } catch (error) {
      console.error(error);

      const errorMessage = extractErrorMessage(error);

      enqueueSnackbar(t(`error.${errorMessage}`), {
        variant: "error",
      });
    }
  };

  const handleDeleteRows = async (selected) => {
    try {
      const result = await deleteAgents(selected);
      enqueueSnackbar(t(`success.${result}`));
      dispatch(deleteAgentsSuccess(selected));
    } catch (error) {
      console.error(error);

      const errorMessage = extractErrorMessage(error);

      enqueueSnackbar(t(`error.${errorMessage}`), {
        variant: "error",
      });
    }
    setSelected([]);
  };

  const handleEditRow = (id) => {
    navigate(PATH_DASHBOARD.agent.edit(id));
  };

  const dataSorted = applySortFilter({
    tableData,
    comparator: getComparator(order, orderBy),
  });

  const denseHeight = dense ? 60 : 80;

  const isNotFound = !dataSorted.length && !isLoading;

  return (
    <Page title={`vendor: ${t("p_vendor.vendor_list")}`}>
      <Container maxWidth={false}>
        <HeaderBreadcrumbs
          heading={t("p_vendor.vendor_list")}
          links={[
            { name: t("params.dashboard"), href: PATH_DASHBOARD.root },
            {
              name: t("p_vendor.vendor"),
              href: PATH_DASHBOARD.agent.root,
            },
            { name: t("p_vendor.vendor_list") },
          ]}
          action={
            <Button
              variant="contained"
              startIcon={<Iconify icon="eva:plus-fill" />}
              component={RouterLink}
              to={PATH_DASHBOARD.agent.add}
            >
              {t("p_vendor.new_vendor")}
            </Button>
          }
        />

        <Card>
          <AgentTableToolbar
            filterName={filterName}
            onFilterName={handleFilterName}
            handleRefresh={handleRefresh}
            isLoading={isLoading}
            placeholder={t("params.search", {
              obj: t("p_vendor.vendor"),
            })}
            loadText={t("params.load")}
            title={t("params.filter_list")}
          />

          <Scrollbar>
            <TableContainer sx={{ minWidth: 800 }}>
              {selected.length > 0 && (
                <TableSelectedActions
                  dense={dense}
                  numSelected={selected.length}
                  rowCount={tableData.length}
                  selectedText={t("params.selected")}
                  onSelectAllRows={(checked) =>
                    onSelectAllRows(
                      checked,
                      tableData.map((row) => row.uid)
                    )
                  }
                  actions={
                    <Tooltip title={t("params.delete")}>
                      <IconButton
                        color="primary"
                        onClick={() => {
                          const confirmation = window.confirm(
                            t("params.deletes_alert")
                          );

                          if (confirmation) {
                            handleDeleteRows(selected);
                          }
                        }}
                      >
                        <Iconify icon={"eva:trash-2-outline"} />
                      </IconButton>
                    </Tooltip>
                  }
                />
              )}

              <Table size={dense ? "small" : "medium"}>
                <TableHeadCustom
                  order={order}
                  orderBy={orderBy}
                  headLabel={TABLE_HEAD}
                  rowCount={tableData.length}
                  numSelected={selected.length}
                  onSort={onSort}
                  onSelectAllRows={(checked) =>
                    onSelectAllRows(
                      checked,
                      tableData.map((row) => row.uid)
                    )
                  }
                />

                <TableBody>
                  {(isLoading ? [...Array(rowsPerPage)] : dataSorted).map(
                    (row, index) =>
                      row ? (
                        <AgentTableRow
                          key={index}
                          row={row}
                          selected={selected.includes(row.uid)}
                          onSelectRow={() => onSelectRow(row.uid)}
                          onDeleteRow={() => {
                            const confirmation = window.confirm(
                              t("params.delete_alert")
                            );

                            if (confirmation) {
                              handleDeleteRow(row.uid);
                            }
                          }}
                          onEditRow={() => handleEditRow(row.uid)}
                          deleteText={t("params.delete")}
                          editText={t("params.edit")}
                        />
                      ) : (
                        !isNotFound && (
                          <TableSkeleton
                            key={index}
                            sx={{ height: denseHeight }}
                          />
                        )
                      )
                  )}

                  <TableEmptyRows
                    height={denseHeight}
                    emptyRows={emptyRows(
                      page,
                      tableData.length,
                      tableData.length + agents.count
                    )}
                  />

                  <TableNoData
                    isNotFound={isNotFound}
                    title={t("params.no_data")}
                  />
                </TableBody>
              </Table>
            </TableContainer>
          </Scrollbar>

          <Box sx={{ position: "relative" }}>
            <TablePagination
              rowsPerPageOptions={[50, 100, 300, 500]}
              component="div"
              count={agents.count}
              rowsPerPage={rowsPerPage}
              page={page}
              onPageChange={onChangePage}
              onRowsPerPageChange={onChangeRowsPerPage}
              labelRowsPerPage={t("params.row_per_page")}
            />

            <FormControlLabel
              control={<Switch checked={dense} onChange={onChangeDense} />}
              label={t("params.dense")}
              sx={{ px: 3, py: 1.5, top: 0, position: { md: "absolute" } }}
            />
          </Box>
        </Card>
      </Container>
    </Page>
  );
}

// ----------------------------------------------------------------------

function applySortFilter({ tableData, comparator }) {
  const stabilizedThis = tableData.map((el, index) => [el, index]);

  stabilizedThis.sort((a, b) => {
    const order = comparator(a[0], b[0]);
    if (order !== 0) return order;
    return a[1] - b[1];
  });

  return stabilizedThis.map((el) => el[0]);
}
