import PropTypes from "prop-types";
import { useState } from "react";
// @mui
import { useTheme } from "@mui/material/styles";
import {
  TableRow,
  Checkbox,
  TableCell,
  Typography,
  MenuItem,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  Button,
  Stack,
  TextField,
  CircularProgress,
  Divider,
} from "@mui/material";
// utils
import { fCurrency } from "../../../../utils/formatNumber";
import { useDispatch } from "../../../../redux/store";
import {
  updateAgentSuccess,
  handleDepositAgent,
  handleDepositAgentHtg,
  handleDepositAgentClp,
  handleWithdrawAgent,
  handleWithdrawAgentHtg,
  handleWithdrawAgentClp,
} from "../../../../redux/slices/agent";
// components
import Label from "../../../../components/Label";
import Iconify from "../../../../components/Iconify";
import { TableMoreMenu } from "../../../../components/table";
import { useTranslation } from "react-i18next";
import handlePermission from "../../../../utils/handlePerm";
import useAuth from "../../../../hooks/useAuth";
import { PERM } from "../../../../guards/perms";

// ----------------------------------------------------------------------

AgentTableRow.propTypes = {
  row: PropTypes.object,
  selected: PropTypes.bool,
  onEditRow: PropTypes.func,
  onSelectRow: PropTypes.func,
  onDeleteRow: PropTypes.func,
  deleteText: PropTypes.string,
  editText: PropTypes.string,
  depositText: PropTypes.string,
  withdrawText: PropTypes.string,
};

export default function AgentTableRow({
  row,
  selected,
  onEditRow,
  onSelectRow,
  onDeleteRow,
  deleteText = "Delete",
  editText = "Edit",
  depositText = "Deposit",
  withdrawText = "Withdraw",
}) {
  const theme = useTheme();
  const dispatch = useDispatch();
  const { t } = useTranslation();
  const { user } = useAuth();

  const permissions_list = user?.user_permissions_list || [];

  const canDelete = handlePermission(permissions_list, PERM.VENDOR_DELETE);
  const canDeposit = handlePermission(permissions_list, PERM.VENDOR_DEPOSIT);
  const canWithdraw = handlePermission(permissions_list, PERM.VENDOR_WITHDRAW);
  const canEdit = handlePermission(permissions_list, PERM.VENDOR_UPDATE);

  const [openMenu, setOpenMenuActions] = useState(null);

  const [amount, setAmount] = useState("");
  const [loading, setLoading] = useState(false);

  const [openMethod, setOpenMethod] = useState(false);
  const [methodMode, setMethodMode] = useState(null);

  const [openForm, setOpenForm] = useState(false);
  const [formConfig, setFormConfig] = useState({
    operation: null,
    currency: null,
  });

  const handleOpenMenu = (event) => {
    setOpenMenuActions(event.currentTarget);
  };

  const handleCloseMenu = () => {
    setOpenMenuActions(null);
  };

  const resetAmount = () => {
    setAmount("");
  };

  const handleOpenMethod = (mode) => {
    setMethodMode(mode);
    setOpenMethod(true);
  };

  const handleCloseMethod = () => {
    setOpenMethod(false);
  };

  const handleChooseOperation = (operation, currency) => {
    setFormConfig({ operation, currency });
    setOpenMethod(false);
    setOpenForm(true);
  };

  const handleCloseForm = () => {
    setOpenForm(false);
    resetAmount();
  };

  const renderAmountField = (placeholder = "Enter amount") => (
    <Stack spacing={2} sx={{ pt: 1 }}>
      <TextField
        value={amount}
        onChange={(event) => setAmount(event.target.value)}
        placeholder={placeholder}
        type="number"
        fullWidth
        variant="outlined"
        inputProps={{ min: 0 }}
      />
    </Stack>
  );

  const dialogPaperProps = {
    sx: {
      borderRadius: 2,
      p: 0,
      minWidth: 360,
    },
  };

  const dialogTitleSx = {
    display: "flex",
    alignItems: "center",
    gap: 1,
    fontWeight: 600,
    pb: 1,
  };

  const dialogContentSx = {
    pt: 1,
  };

  const dialogActionsSx = {
    px: 3,
    pb: 2,
    pt: 1,
  };

  const getTitleKey = () => {
    const { operation, currency } = formConfig;
    if (!operation || !currency) return "";
    const op = operation === "DEPOSIT" ? "deposit" : "withdraw";
    const curr = currency.toLowerCase(); // usd / htg / clp
    return `remesas.${op}_${curr}`;
  };

  const getPlaceholder = () => {
    const { currency } = formConfig;
    if (!currency) return "Enter amount";
    return `Enter amount in ${currency}`;
  };

  const getIconProps = () => {
    const { operation, currency } = formConfig;
    if (!operation) return { icon: "mdi:cash", color: "primary.main" };

    if (operation === "DEPOSIT") {
      if (currency === "USD")
        return { icon: "mdi:cash-plus", color: "success.main" };
      if (currency === "HTG")
        return { icon: "mdi:cash-plus", color: "#ff8612" };
      if (currency === "CLP")
        return { icon: "mdi:cash-plus", color: "#ff9800" };
    } else {
      if (currency === "USD")
        return { icon: "mdi:cash-minus", color: "error.main" };
      if (currency === "HTG")
        return { icon: "mdi:cash-minus", color: "#b80d57" };
      if (currency === "CLP")
        return { icon: "mdi:cash-minus", color: "#ff9800" };
    }
    return { icon: "mdi:cash", color: "primary.main" };
  };

  const handleSubmit = async (id) => {
    if (!amount) {
      alert("Avant de continuer, entrer le montant svp.");
      return;
    }

    const { operation, currency } = formConfig;
    if (!operation || !currency) return;

    setLoading(true);
    try {
      const values = { user_id: id, amount };
      let res = null;

      if (operation === "DEPOSIT") {
        if (currency === "USD") res = await handleDepositAgent(values);
        if (currency === "HTG") res = await handleDepositAgentHtg(values);
        if (currency === "CLP") res = await handleDepositAgentClp(values);
      } else {
        if (currency === "USD") res = await handleWithdrawAgent(values);
        if (currency === "HTG") res = await handleWithdrawAgentHtg(values);
        if (currency === "CLP") res = await handleWithdrawAgentClp(values);
      }

      if (res?.user) {
        dispatch(updateAgentSuccess(res.user));
      }

      resetAmount();
      setOpenForm(false);
    } catch (error) {
      console.error(error);
    } finally {
      setLoading(false);
    }
  };

  const { icon: formIcon, color: formColor } = getIconProps();
  const titleKey = getTitleKey();

  return (
    <>
      <TableRow hover selected={selected}>
        <TableCell padding="checkbox">
          <Checkbox checked={selected} onClick={onSelectRow} />
        </TableCell>

        <TableCell padding="checkbox" />

        <TableCell align="center">{row.full_name}</TableCell>
        <TableCell align="center">USD {fCurrency(row.balance)}</TableCell>
        <TableCell align="center">HTG {fCurrency(row.balance_htg)}</TableCell>
        <TableCell align="center">CLP {fCurrency(row.balance_clp)}</TableCell>
        <TableCell align="center">{row.supervisor_name || "-"}</TableCell>
        <TableCell align="center">{row.phone || "-"}</TableCell>
        <TableCell align="center">{row.country_name}</TableCell>
        <TableCell align="center">{row.city}</TableCell>
        <TableCell align="center">{row.address}</TableCell>

        <TableCell align="center">
          <Label
            variant={theme.palette.mode === "light" ? "ghost" : "filled"}
            color={!row.transfer_opt_in ? "error" : "success"}
            sx={{ textTransform: "capitalize" }}
          >
            {row.transfer_opt_in ? t("params.yes") : t("params.no")}
          </Label>
        </TableCell>

        <TableCell align="center">
          <Label
            variant={theme.palette.mode === "light" ? "ghost" : "filled"}
            color={row.is_block ? "error" : "success"}
            sx={{ textTransform: "capitalize" }}
          >
            {row.is_block ? t("params.yes") : t("params.no")}
          </Label>
        </TableCell>

        <TableCell align="center">
          <Label
            variant={theme.palette.mode === "light" ? "ghost" : "filled"}
            color={!row.jackpot_opt_in ? "error" : "success"}
            sx={{ textTransform: "capitalize" }}
          >
            {row.jackpot_opt_in ? t("params.yes") : t("params.no")}
          </Label>
        </TableCell>

        <TableCell align="center">
          {canDeposit && (
            <Button
              variant="contained"
              color="primary"
              size="small"
              onClick={() => handleOpenMethod("DEPOSIT")}
            >
              {t("remesas.deposit")}
            </Button>
          )}
        </TableCell>

        <TableCell align="right">
          <TableMoreMenu
            open={openMenu}
            onOpen={handleOpenMenu}
            onClose={handleCloseMenu}
            actions={
              <>
                {canDelete && (
                  <MenuItem
                    onClick={() => {
                      handleCloseMenu();
                      onDeleteRow();
                    }}
                    sx={{ color: "error.main" }}
                  >
                    <Iconify icon={"eva:trash-2-outline"} />
                    {deleteText}
                  </MenuItem>
                )}

                {canEdit && (
                  <MenuItem
                    onClick={() => {
                      onEditRow();
                      handleCloseMenu();
                    }}
                  >
                    <Iconify icon={"eva:edit-fill"} />
                    {editText}
                  </MenuItem>
                )}

                {canDeposit && (
                  <MenuItem
                    onClick={() => {
                      handleOpenMethod("DEPOSIT");
                      handleCloseMenu();
                    }}
                    sx={{ color: "green" }}
                  >
                    <Iconify icon={"mdi:cash-plus"} />
                    {t("remesas.deposit")}
                  </MenuItem>
                )}

                {canWithdraw && (
                  <MenuItem
                    onClick={() => {
                      handleOpenMethod("WITHDRAW");
                      handleCloseMenu();
                    }}
                    sx={{ color: "red" }}
                  >
                    <Iconify icon={"mdi:cash-minus"} />
                    {t("remesas.withdraw")}
                  </MenuItem>
                )}
              </>
            }
          />
        </TableCell>
      </TableRow>

      <Dialog
        open={openMethod}
        onClose={handleCloseMethod}
        aria-labelledby="method-dialog-title"
        PaperProps={dialogPaperProps}
      >
        <DialogTitle id="method-dialog-title" sx={dialogTitleSx}>
          <Iconify
            icon={methodMode === "DEPOSIT" ? "mdi:cash-plus" : "mdi:cash-minus"}
          />
          {t("remesas.choose_method")}
        </DialogTitle>
        <Divider />
        <DialogContent sx={dialogContentSx}>
          <Stack spacing={2} sx={{ pt: 1, minWidth: 280 }}>
            <Typography variant="body2" color="text.secondary">
              {t("remesas.choose_method_desc")}
            </Typography>

            <Button
              fullWidth
              variant="contained"
              onClick={() =>
                handleChooseOperation(methodMode || "DEPOSIT", "USD")
              }
              startIcon={
                <Iconify
                  icon={
                    methodMode === "DEPOSIT"
                      ? "mdi:cash-plus"
                      : "mdi:cash-minus"
                  }
                />
              }
            >
              {methodMode === "DEPOSIT"
                ? t("remesas.deposit_usd")
                : t("remesas.withdraw_usd")}
            </Button>

            <Button
              fullWidth
              variant="contained"
              color="secondary"
              onClick={() =>
                handleChooseOperation(methodMode || "DEPOSIT", "HTG")
              }
              startIcon={
                <Iconify
                  icon={
                    methodMode === "DEPOSIT"
                      ? "mdi:cash-plus"
                      : "mdi:cash-minus"
                  }
                />
              }
            >
              {methodMode === "DEPOSIT"
                ? t("remesas.deposit_htg")
                : t("remesas.withdraw_htg")}
            </Button>

            <Button
              fullWidth
              variant="contained"
              color="warning"
              onClick={() =>
                handleChooseOperation(methodMode || "DEPOSIT", "CLP")
              }
              startIcon={
                <Iconify
                  icon={
                    methodMode === "DEPOSIT"
                      ? "mdi:cash-plus"
                      : "mdi:cash-minus"
                  }
                />
              }
            >
              {methodMode === "DEPOSIT"
                ? t("remesas.deposit_clp")
                : t("remesas.withdraw_clp")}
            </Button>
          </Stack>
        </DialogContent>
        <DialogActions sx={dialogActionsSx}>
          <Button onClick={handleCloseMethod}>
            {t("remesas.common.cancel")}
          </Button>
        </DialogActions>
      </Dialog>

      <Dialog
        open={openForm}
        onClose={handleCloseForm}
        aria-labelledby="form-dialog-title"
        PaperProps={dialogPaperProps}
      >
        <DialogTitle id="form-dialog-title" sx={dialogTitleSx}>
          <Iconify icon={formIcon} color={formColor} />
          {titleKey ? t(titleKey) : ""}
          {` : ${row.full_name}`}
        </DialogTitle>
        <Divider />
        <DialogContent sx={dialogContentSx}>
          {renderAmountField(getPlaceholder())}
        </DialogContent>
        <DialogActions sx={dialogActionsSx}>
          <Button onClick={handleCloseForm} disabled={loading}>
            {t("remesas.common.cancel")}
          </Button>
          <Button
            onClick={() => handleSubmit(row.uid)}
            color="primary"
            variant="contained"
            disabled={loading}
          >
            {loading ? (
              <CircularProgress size={24} />
            ) : (
              t("remesas.common.confirm")
            )}
          </Button>
        </DialogActions>
      </Dialog>
    </>
  );
}
